import { Component, OnInit, ViewChild } from '@angular/core';
import { ApplicationService, IApplication } from '@c8y/client';
import { C8yStepper, WizardComponent } from '@c8y/ngx-components';
import { BehaviorSubject } from 'rxjs';
import { EcosystemService } from '../ecosystem.service';

@Component({
  selector: 'c8y-duplicate-application',
  templateUrl: './duplicate-application.component.html'
})
export class DuplicateApplicationComponent implements OnInit {
  @ViewChild(C8yStepper, { static: true })
  stepper: C8yStepper;
  duplicatedApp: IApplication;
  selectedApp: BehaviorSubject<IApplication> = new BehaviorSubject<IApplication>(null);
  existingApps: IApplication[] = [];
  directlyMoveToSecondStep = false;

  constructor(
    private ecosystemService: EcosystemService,
    private applicationService: ApplicationService,
    private wizardComponent: WizardComponent
  ) {}

  async ngOnInit() {
    this.existingApps = await this.ecosystemService.getWebApplications();
    if (this.selectedApp.value) {
      this.directlyMoveToSecondStep = true;
    }
  }

  onSelectedApp(app: IApplication) {
    this.selectedApp.next(app);
  }

  getHref(app: IApplication): string {
    return this.applicationService.getHref(app);
  }

  done() {
    this.wizardComponent.close();
  }
}
