import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PackageType } from '@c8y/ngx-components';
import { PACKAGE_TYPE_LABELS } from './ecosystem.constants';

@Pipe({ name: 'translatePackageLabel' })
export class TranslatePackageLabelPipe implements PipeTransform {
  constructor(private translate: TranslateService) {}

  transform(rawPackageLabel: PackageType.OFFICIAL | PackageType.COMMUNITY | string): string {
    const isOneOfTheKnownOnes = !!PACKAGE_TYPE_LABELS[rawPackageLabel];
    if (isOneOfTheKnownOnes) {
      return this.translate.instant(PACKAGE_TYPE_LABELS[rawPackageLabel].label);
    }
    return rawPackageLabel;
  }
}
