import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationService, IApplication, IApplicationVersion } from '@c8y/client';
import {
  AlertService,
  GainsightService,
  gettext,
  PackageType,
  PluginsService,
  Wizard,
  WizardComponent,
  WizardConfig,
  WizardModalService
} from '@c8y/ngx-components';
import { ModalOptions } from 'ngx-bootstrap/modal';
import { BehaviorSubject } from 'rxjs';
import { EcosystemService } from './ecosystem.service';
import { ApplicationState, PRODUCT_EXPERIENCE } from './ecosystem.model';
import { DuplicateApplicationComponent } from './duplicate-application/duplicate-application.component';
import { APP_STATE, PACKAGE_TYPE_LABELS } from './ecosystem.constants';
import { AppFilterProps } from './list-filters/list-filters.model';

@Component({
  selector: 'c8y-application-card',
  templateUrl: './application-card.component.html'
})
export class ApplicationCardComponent implements OnInit {
  CURRENT_LOCATION = location.href;

  @Input() app: IApplication;
  @Input() canEdit = true;
  @Output() onAppDeleted: EventEmitter<void> = new EventEmitter();
  @Output() onAppCloned: EventEmitter<void> = new EventEmitter();
  appState: ApplicationState;
  packageContentState: ApplicationState;
  packageType: PackageType;
  canOpenInBrowser: boolean;
  disableOpenInBrowser: boolean;
  canDelete: boolean;
  isPackage: boolean;
  isPlugin: boolean;
  isFeature: boolean;
  isExternal: boolean;
  isMicroservice: boolean;
  canClone: boolean;
  isUnpacked: boolean;
  shouldUpgradePackage: boolean;
  blueprintApplicationVersion: IApplicationVersion[];
  latestApplicationVersion: IApplicationVersion;
  isShowVersion: boolean;
  isOwnedByManagementTenant: boolean;
  get openButtonTitle() {
    return !this.disableOpenInBrowser
      ? gettext('Open')
      : gettext('This application is overridden.');
  }

  readonly packageTypeLabels = PACKAGE_TYPE_LABELS;
  readonly PACKAGE_TYPE = PackageType;
  readonly UNPACKED_LABEL = APP_STATE.UNPACKED.label;
  readonly CANNOT_DELETE_HINT = gettext(
    `Subscribed or current applications can't be deleted. Delete the application on the parent tenant or unsubscribe it from the current.`
  );
  readonly PACKAGE_CONTENT_UNDETERMINED_LABEL = gettext(
    'Package contents could not be determined.'
  );

  constructor(
    private applicationService: ApplicationService,
    private ecosystemService: EcosystemService,
    private alertService: AlertService,
    private router: Router,
    private wizardModalService: WizardModalService,
    private alert: AlertService,
    private pluginService: PluginsService,
    private gainsightService: GainsightService
  ) {}

  async ngOnInit() {
    this.appState = this.ecosystemService.getAppState(this.app);
    this.canOpenInBrowser = this.ecosystemService.canOpenAppInBrowser(this.app);
    this.packageType = this.pluginService.getPackageType(this.app);
    this.disableOpenInBrowser =
      this.canOpenInBrowser && (await this.ecosystemService.isOverwrittenByCustomApp(this.app));
    this.canDelete = await this.ecosystemService.canDeleteApp(this.app);
    this.isPackage = this.ecosystemService.isPackage(this.app);
    this.packageContentState = this.ecosystemService.getPackageContentState(this.app);
    this.isPlugin = this.ecosystemService.isPlugin(this.app);
    this.isFeature = this.ecosystemService.isFeature(this.app);
    this.isMicroservice = this.ecosystemService.isMicroservice(this.app);
    this.isExternal = this.ecosystemService.isExternal(this.app);
    this.canClone = !this.isMicroservice && !this.isPlugin;
    this.isUnpacked = this.ecosystemService.isUnpacked(this.app);
    this.isShowVersion = this.shouldShowVersion();
    this.app.filterProps = {
      availability: this.appState?.label,
      content: this.packageContentState?.label,
      type: this.packageType
    } as AppFilterProps;
    await this.applicationUpdateCheck();
  }

  shouldShowVersion() {
    return (
      this.app.manifest?.version &&
      (!!this.app.manifest?.isPackage || this.isMicroservice || !!this.app.manifest?.source)
    );
  }

  detail() {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.APPLICATION_CARD, {
      component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_CARD,
      action: PRODUCT_EXPERIENCE.APPLICATIONS.ACTIONS.EDIT,
      url: this.CURRENT_LOCATION
    });

    if (this.isMicroservice) {
      this.router.navigateByUrl(`/ecosystem/microservice/microservices/${this.app.id}`);
      return;
    }

    if (this.isPackage) {
      this.router.navigateByUrl(`/ecosystem/extension/extensions/${this.app.id}`);
      return;
    }

    if (this.isFeature) {
      this.router.navigateByUrl(`/ecosystem/application/features/${this.app.id}`);
      return;
    }

    this.router.navigateByUrl(`/ecosystem/application/applications/${this.app.id}`);
  }

  openApp() {
    this.ecosystemService.openApp(this.app);
  }

  async delete() {
    try {
      await this.ecosystemService.deleteApp(this.app);
      this.onAppDeleted.emit();
      this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.APPLICATION_CARD, {
        component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_CARD,
        action: PRODUCT_EXPERIENCE.APPLICATIONS.ACTIONS.DELETE,
        url: this.CURRENT_LOCATION
      });
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
        this.gainsightService.triggerEvent(
          PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.APPLICATION_CARD,
          {
            component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_CARD,
            result: PRODUCT_EXPERIENCE.APPLICATIONS.RESULTS.SERVER_FAILURE,
            url: this.CURRENT_LOCATION
          }
        );
      }
    }
  }

  async clone() {
    const wizardConfig: WizardConfig = {
      headerText: 'Add Application',
      headerIcon: 'c8y-atom'
    };

    const initialState: Wizard<DuplicateApplicationComponent> = {
      wizardConfig,
      componentInitialState: {
        selectedApp: new BehaviorSubject(this.app)
      },
      id: 'duplicateApplication'
    };

    const modalOptions: ModalOptions<WizardComponent> = { initialState };

    const modalRef = this.wizardModalService.show(modalOptions);
    modalRef.content.onClose.subscribe(() => {
      this.onAppCloned.next();
    });

    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.APPLICATIONS.EVENTS.APPLICATION_CARD, {
      component: PRODUCT_EXPERIENCE.APPLICATIONS.COMPONENTS.APPLICATION_CARD,
      action: PRODUCT_EXPERIENCE.APPLICATIONS.ACTIONS.CLONE,
      url: this.CURRENT_LOCATION
    });
  }

  private async applicationUpdateCheck(): Promise<void> {
    if (this.app.manifest?.source) {
      try {
        this.blueprintApplicationVersion = (
          await this.applicationService.listVersions(this.app.manifest?.source)
        ).data;

        this.latestApplicationVersion = this.ecosystemService.getApplicationVersionObjectByTag(
          this.blueprintApplicationVersion,
          'latest'
        );
        this.shouldUpgradePackage = this.ecosystemService.shouldUpgradePackage(
          this.app.manifest?.version,
          this.latestApplicationVersion
        );
      } catch (error) {
        if (!this.blueprintApplicationVersion) {
          return;
        }
        this.alert.addServerFailure(error);
      }
    }
  }
}
