import { Component, forwardRef, OnChanges, Input, SimpleChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ApplicationService, IApplicationVersion, IResultList } from '@c8y/client';
import { gettext, PluginsService } from '@c8y/ngx-components';
import { BehaviorSubject, Observable, combineLatest, merge } from 'rxjs';
import { filter, map, shareReplay, switchMap } from 'rxjs/operators';

@Component({
  selector: 'c8y-package-version-select',
  templateUrl: './package-version-select.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => PackageVersionSelectComponent)
    }
  ]
})
export class PackageVersionSelectComponent implements OnChanges, ControlValueAccessor {
  @Input() label = gettext('Use plugin version');
  @Input() packageContextPath: string;
  @Input() packageId: string | number;
  selectedVersion: IApplicationVersion;
  onInput$: BehaviorSubject<string> = new BehaviorSubject<string>('');
  versions$: Observable<IResultList<IApplicationVersion>>;
  isDisabled = false;
  private packageContextPath$ = new BehaviorSubject('');
  private packageId$ = new BehaviorSubject<string | number>('');
  private onChange: (version: IApplicationVersion) => void;
  private onTouched: () => void;

  constructor(
    private applicationService: ApplicationService,
    private pluginsService: PluginsService
  ) {
    const packageIdFromContextPath$ = this.packageContextPath$.pipe(
      filter(path => !!path),
      switchMap(path => this.getPackageIdForContextPath(path))
    );
    const packageId$ = merge(packageIdFromContextPath$, this.packageId$.pipe(filter(id => !!id)));
    const packageVersions$ = packageId$.pipe(
      switchMap(id => this.getPackageVersions(id)),
      shareReplay(1)
    );
    this.versions$ = combineLatest([packageVersions$, this.onInput$.asObservable()]).pipe(
      map(([resultList, filterStr]) => this.applyFilterToResultList(resultList, filterStr))
    );
  }

  writeValue(obj: any): void {
    this.selectedVersion = obj;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.packageContextPath) {
      this.packageContextPath$.next(this.packageContextPath);
    }

    if (changes.packageId) {
      this.packageId$.next(this.packageId);
    }
  }

  async getPackageVersions(packageId: string | number): Promise<IResultList<IApplicationVersion>> {
    return await this.applicationService.listVersions(packageId);
  }

  onVersionSelect(version: IApplicationVersion) {
    this.selectedVersion = version;
    if (this.onChange) {
      this.onChange(version);
    }
    if (this.onTouched) {
      this.onTouched();
    }
  }

  private async getPackageIdForContextPath(contextPath: string): Promise<string | number> {
    const { data: application } = await this.applicationService.getManifestOfContextPath(
      contextPath
    );
    const packageAppId = application.id;
    return packageAppId;
  }

  private setInitialValueForInput(versions: IApplicationVersion[]) {
    if (!this.selectedVersion && versions.length > 0) {
      const latest = versions.find(v => v.tags.includes('latest'));
      this.selectedVersion = latest || versions[0];
      if (this.onChange) {
        this.onChange(this.selectedVersion);
      }
    }
  }

  private filterAppVersions(
    appVersions: IApplicationVersion[],
    filterStr: string
  ): IApplicationVersion[] {
    return filterStr === ''
      ? appVersions
      : appVersions.filter(
          appVersion =>
            appVersion.version.includes(filterStr) ||
            appVersion.tags?.some(tag => tag.includes(filterStr))
        );
  }

  private applyFilterToResultList(
    resultList: IResultList<IApplicationVersion>,
    filterStr: string
  ): IResultList<IApplicationVersion> {
    const versionsFilteredByStr = this.filterAppVersions(resultList.data, filterStr);
    const sortedAppVersions = this.pluginsService.sortVersions(
      {
        list: versionsFilteredByStr,
        path: ['version']
      },
      'desc'
    );

    this.setInitialValueForInput(sortedAppVersions);
    return { data: sortedAppVersions, res: resultList.res };
  }
}
