import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IApplication } from '@c8y/client';
import { C8yStepper, WizardComponent } from '@c8y/ngx-components';
import { ApplicationState } from '../../ecosystem.model';
import { EcosystemService } from '../../ecosystem.service';

@Component({
  selector: 'c8y-duplicate-application-list',
  templateUrl: './duplicate-application-list.component.html'
})
export class DuplicateApplicationListComponent {
  @Input() stepper: C8yStepper;
  @Input() existingApps: IApplication[];
  @Output() onSelectedApp = new EventEmitter<IApplication>();
  appState: ApplicationState;

  constructor(
    public ecosystemService: EcosystemService,
    private wizardComponent: WizardComponent
  ) {}

  select(selectedApp: IApplication) {
    this.onSelectedApp.emit(selectedApp);
    this.stepper.next();
  }

  cancel() {
    this.wizardComponent.close();
  }

  back() {
    this.wizardComponent.reset();
  }
}
